<template>
    <div class="clearfix">
        <slot></slot>
    </div>
</template>
<script>
export default {
    props:[
        "isLastPage"        //是否最后一页
    ],
    created(){
          this.$emit("ajax-query", this.pagepation);
        //ajax请求
    },
  updated() {
        let self=this,
        // listnumber = this.pageNumber,    //一页条数
        // rownumber = 3,  //行显示数
        // offsetTop = 200, //第一条数据距离上部的距离
        offsetTop = this.$el.offsetTop, //第一条数据距离上部的距离
        listHeight = this.$el.children.length>0?
        this.$el.children[0].offsetHeight:0 ,  //单个元素高度
        listTop = this.$el.children.length>0?
        this.$el.children[0].offsetTop:offsetTop, //距离顶部高度
        // listTotalHeight = listnumber/rownumber*listHeight+listTop;  //总高度
        listTotalHeight =  this.$el.offsetHeight;  //总高度
            $(".view").on("scroll",function(){
                let scrollTop = $(this).scrollTop();
                let windowHeight = $(document).height();
                // console.log(scrollTop + windowHeight)
                if(!self.isLastPage){
                    if(scrollTop + windowHeight + 60 >= listTotalHeight){
                        // ajax请求
                        $(".view").off("scroll")
                        self.pagepation.pageNum ++;     
                        self.$emit("ajax-query", self.pagepation);
                    }
                }
            })
  },
  data() {
    return {
        pagepation: {
            //翻页配置
            pageNum: 1,
            pageSize: 15
        },
    };
  },
  methods: {

  }
};
</script>



